<template>
  <div ref="plot" class="plotly-graph"></div>
</template>

<script>
import Plotly from "plotly.js-dist-min";

export default {
  props: {
    dataPoints: {
      type: Array,
      required: true,
      default: () => [],
    },
    plane: {
      type: String,
      required: true,
      default: "x = 0", // Значение по умолчанию
    },
  },
  watch: {
    dataPoints: {
      handler(newVal) {
        this.$nextTick(() => {
          if (newVal.length > 0) {
            this.renderPlot();
          } else {
            this.clearPlot();
          }
        });
      },
      deep: true,
      immediate: true,
    },
    plane() {
      this.$nextTick(() => {
        if (this.dataPoints.length > 0) {
          this.renderPlot();
        }
      });
    },
  },
  mounted() {
    this.$nextTick(() => {
      if (this.dataPoints.length > 0) {
        this.renderPlot();
      }
    });
  },
  methods: {
    /**
     * Форматирует число в строку без научной нотации.
     * @param {number} num - Число для форматирования.
     * @returns {string} - Отформатированное число.
     */
    formatNumber(num) {
      if (num === 0) return "0";
      return num
        .toLocaleString(undefined, {
          minimumFractionDigits: 12,
          maximumFractionDigits: 12,
        })
        .replace(/\.?0+$/, "");
    },

    /**
     * Метод для отрисовки графика.
     */
    renderPlot() {
      if (!this.$refs.plot) {
        console.error("Ref 'plot' is not available.");
        return;
      }

      // Определение осей на основе выбранной плоскости
      const planeMapping = {
        "x = 0": {
          x: "y",
          y: "z",
          titleX: "Y (10³ km)",
          titleY: "Z (10³ km)",
        },
        "y = 0": {
          x: "x",
          y: "z",
          titleX: "X (10³ km)",
          titleY: "Z (10³ km)",
        },
        "z = 0": {
          x: "x",
          y: "y",
          titleX: "X (10³ km)",
          titleY: "Y (10³ km)",
        },
        "vx = 0": {
          x: "vy",
          y: "vz",
          titleX: "Vy (10³ km/s)",
          titleY: "Vz (10³ km/s)",
        },
        "vy = 0": {
          x: "vx",
          y: "vz",
          titleX: "Vx (10³ km/s)",
          titleY: "Vz (10³ km/s)",
        },
        "vz = 0": {
          x: "vx",
          y: "vy",
          titleX: "Vx (10³ km/s)",
          titleY: "Vy (10³ km/s)",
        },
      };

      const selectedPlane = this.plane || "x = 0"; // Убедимся, что plane не null
      const axes = planeMapping[selectedPlane];

      if (!axes) {
        console.error(`Unknown plane: ${selectedPlane}`);
        return;
      }

      // Создаем массив trace-ов, по одному для каждой орбиты
      const data = this.dataPoints.map((orbitData, index) => {
        const scaledDataPoints = orbitData.v.map((point) => ({
          x: point[axes.x] / 1000, // Масштабируем выбранную ось X
          y: point[axes.y] / 1000, // Масштабируем выбранную ось Y
          actualX: point.x,
          actualY: point.y,
          actualZ: point.z,
          vx: point.vx,
          vy: point.vy,
          vz: point.vz,
        }));

        const x = scaledDataPoints.map((p) => p.x);
        const y = scaledDataPoints.map((p) => p.y);

        // Форматирование ховер-текста без научной нотации
        const hoverText = scaledDataPoints.map((p) => {
          return (
            `X: ${this.formatNumber(p.actualX)} km<br>` +
            `Y: ${this.formatNumber(p.actualY)} km<br>` +
            `Z: ${this.formatNumber(p.actualZ)} km<br>` +
            `Vx: ${this.formatNumber(p.vx)} km/s<br>` +
            `Vy: ${this.formatNumber(p.vy)} km/s<br>` +
            `Vz: ${this.formatNumber(p.vz)} km/s`
          );
        });

        // Генерируем уникальный цвет для каждой орбиты
        const color = `hsl(${
          (index * 360) / this.dataPoints.length
        }, 100%, 50%)`;

        return {
          x: x,
          y: y,
          mode: "markers",
          type: "scatter",
          name: `Орбита ${orbitData.orbit_id}`,
          marker: { size: 6, color: color },
          text: hoverText,
          hoverinfo: "text",
        };
      });

      const layout = {
        title: `Пуанкаре секции (${selectedPlane})`,
        xaxis: {
          title: axes.titleX,
          tickformat: ",.0f",
          exponentformat: "none",
          zeroline: false,
          showgrid: true,
        },
        yaxis: {
          title: axes.titleY,
          tickformat: ",.0f",
          exponentformat: "none",
          zeroline: false,
          showgrid: true,
        },
        showlegend: false, // Отключаем легенду
        width: 700,
        height: 500,
        hovermode: "closest",
      };

      const config = {
        displayModeBar: false,
        responsive: true,
      };

      Plotly.react(this.$refs.plot, data, layout, config);
    },

    /**
     * Метод для очистки графика.
     */
    clearPlot() {
      if (this.$refs.plot) {
        Plotly.purge(this.$refs.plot);
      } else {
        console.warn("Plot reference is not available for clearing.");
      }
    },
  },
};
</script>

<style scoped>
.plotly-graph {
  width: 700px;
  height: 500px;
}
</style>
