<template>
  <div :id="chartId" class="plotly-chart"></div>
</template>

<script>
import Plotly from "plotly.js-dist-min";

export default {
  props: {
    data: {
      type: Array,
      required: true,
    },
    projection: {
      type: String,
      required: true,
      validator: (value) => ["xy", "xz", "yz"].includes(value),
    },
  },
  data() {
    return {
      chartId: `plotly-chart-${Math.random()
        .toString(36)
        .substr(2, 9)}`,
    };
  },
  mounted() {
    this.plotData();
  },
  watch: {
    data() {
      this.plotData();
    },
    projection() {
      this.plotData();
    },
  },
  methods: {
    plotData() {
      if (!this.data || this.data.length === 0) {
        console.error("No data provided for 2D projection.");
        return;
      }

      let xAxis, yAxis, xLabel, yLabel;
      if (this.projection === "xy") {
        xAxis = this.data.map((point) => point.x / 1000);
        yAxis = this.data.map((point) => point.y / 1000);
        xLabel = "X, 10³ км";
        yLabel = "Y, 10³ км";
      } else if (this.projection === "xz") {
        xAxis = this.data.map((point) => point.x / 1000);
        yAxis = this.data.map((point) => point.z / 1000);
        xLabel = "X, 10³ км";
        yLabel = "Z, 10³ км";
      } else if (this.projection === "yz") {
        xAxis = this.data.map((point) => point.y / 1000);
        yAxis = this.data.map((point) => point.z / 1000);
        xLabel = "Y, 10³ км";
        yLabel = "Z, 10³ км";
      }

      const trace = {
        x: xAxis,
        y: yAxis,
        mode: "lines",
        line: {
          color: "blue",
          width: 2,
        },
        hoverinfo: "none",
      };

      const layout = {
        xaxis: {
          title: { text: xLabel, standoff: 10 },
          zeroline: false,
          tickformat: ",.0f",
        },
        yaxis: {
          title: { text: yLabel, standoff: 10 },
          zeroline: false,
          tickformat: ",.0f",
        },
        margin: { l: 40, r: 10, b: 30, t: 10 },
      };

      Plotly.newPlot(this.chartId, [trace], layout, {
        responsive: true,
      });
    },
  },
};
</script>

<style scoped>
.plotly-chart {
  width: 100%;
  height: 300px;
}
</style>
