<template>
  <div class="container">
    <!-- Индикатор загрузки -->
    <div v-if="isLoading" class="loading-message">
      <div class="spinner"></div>
      <p>Загрузка данных...</p>
    </div>
    <!-- Сообщение об ошибке -->
    <div v-else-if="errorMessage" class="error-message">
      <p>{{ errorMessage }}</p>
    </div>
    <!-- График -->
    <div v-else-if="dataPoints.length > 0" class="content">
      <!-- Компонент графика -->
      <GraphForParameterChange
        :dataPoints="dataPoints"
        :paramX="paramX"
        :paramY="paramY"
      />
    </div>
  </div>
</template>

<script>
import GraphForParameterChange from "./GraphForParameterChange.vue";

export default {
  components: {
    GraphForParameterChange,
  },
  props: {
    family: {
      type: String,
      required: false,
    },
    paramX: {
      type: String,
      required: false,
    },
    paramY: {
      type: String,
      required: false,
    },
  },
  data() {
    return {
      isLoading: false,
      errorMessage: "",
      dataPoints: [],
    };
  },
  computed: {
    fetchParams() {
      return {
        family: this.family,
        paramX: this.paramX,
        paramY: this.paramY,
      };
    },
  },
  watch: {
    fetchParams: {
      handler() {
        if (
          !this.fetchParams.family ||
          !this.fetchParams.paramX ||
          !this.fetchParams.paramY
        ) {
          return;
        }
        this.fetchData();
      },
      deep: true,
    },
  },

  mounted() {
    if (this.family && this.paramX && this.paramY) {
      this.fetchData();
    }
  },
  methods: {
    async fetchData() {
      if (!this.family || !this.paramX || !this.paramY) {
        this.errorMessage =
          "Пожалуйста, выберите семейство и параметры для осей X и Y";
        return;
      }

      this.isLoading = true;
      this.errorMessage = "";
      this.dataPoints = [];

      try {
        const response = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/orbits/family/${encodeURIComponent(
            this.family
          )}`
        );

        if (!response.ok) {
          throw new Error("Ошибка при загрузке данных");
        }

        const data = await response.json();

        this.dataPoints = data || [];
      } catch (error) {
        console.error("Error fetching data:", error);
        this.errorMessage = error.message;
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>
