<!-- NearestOrbitPoincare.vue -->
<template>
  <div class="container">
    <!-- Индикатор загрузки -->
    <div v-if="combinedIsLoading" class="loading-message">
      <div class="spinner"></div>
      <p>Загрузка данных графика...</p>
    </div>

    <div v-else class="content">
      <div class="info-2">
        <h3>Визуализация орбиты</h3>
        <div>
          <div v-if="noOrbitInfoFound" class="no-orbits-message">
            <p>К сожалению, данные графика орбиты не найдены.</p>
          </div>
          <div v-else-if="trajectoryLoading" class="loading-message">
            <div class="spinner"></div>
            <p>Загрузка данных траектории...</p>
          </div>
          <div v-else-if="orb4.length > 0">
            <!-- Использование дочернего компонента для графика -->
            <PlotlyGraph :dataPoints="orb4" :plane="plane" />
          </div>
          <div v-else class="no-orbits-message">
            <p>Данные траектории отсутствуют.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import PlotlyGraph from "./PlotlyGraph.vue"; // Убедитесь, что путь корректен

export default {
  components: {
    PlotlyGraph,
  },
  props: {
    parentLoading: {
      type: Boolean,
      default: false,
    },
    poincarePlane: {
      type: String,
      default: "x = 0",
    },
  },
  data() {
    return {
      orb4: [], // Массив точек траектории
      noOrbitInfoFound: false,
      localLoading: false, // Локальное состояние загрузки
      plane: this.poincarePlane || "x = 0", // Параметр плоскости с проверкой на null
      trajectoryLoading: false, // Состояние загрузки траектории
    };
  },
  computed: {
    ...mapGetters(["orbitId"]),
    combinedIsLoading() {
      return (
        this.parentLoading ||
        this.localLoading ||
        this.trajectoryLoading
      );
    },
  },
  watch: {
    orbitId(newVal) {
      if (newVal) {
        this.loadOrbitData(newVal);
      } else {
        this.noOrbitInfoFound = true;
        this.resetData();
      }
    },
    poincarePlane(newVal) {
      this.plane = newVal || "x = 0"; // Проверка на null
      if (this.orbitId) {
        this.loadOrbitData(this.orbitId);
      }
    },
  },
  mounted() {
    if (this.orbitId) {
      this.loadOrbitData(this.orbitId);
    } else {
      this.noOrbitInfoFound = true;
    }
  },
  methods: {
    resetData() {
      this.orb4 = [];
      // График теперь управляется дочерним компонентом
    },

    /**
     * Метод для загрузки данных траектории орбиты.
     * @param {number|string} id - ID орбиты.
     */
    async loadOrbitData(id) {
      console.log(`Загрузка данных орбиты с ID: ${id}`);
      this.localLoading = true; // Начало локальной загрузки
      try {
        // Выполняем запрос к API для получения данных орбиты
        console.log(`Запрос данных орбиты с ID: ${id}`);
        let response = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/orbits/${id}`,
          {
            method: "GET",
          }
        );
        console.log(`Ответ на запрос орбиты: ${response.status}`);
        if (!response.ok) {
          this.noOrbitInfoFound = true;
          throw new Error("Failed to fetch orbit data");
        }
        let result = await response.json();
        console.log("Полученные данные орбиты:", result);

        if (!result || !result.id) {
          this.noOrbitInfoFound = true;
          throw new Error("Invalid orbit data");
        }

        this.noOrbitInfoFound = false;

        // Выполняем запрос траектории орбиты
        console.log(`Запрос траектории для орбиты ID: ${result.id}`);

        this.trajectoryLoading = true;

        // Первый запрос: Poincaré секции
        let response2 = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/poincare_sections/${result.id}/${this.plane}`,
          {
            method: "GET",
          }
        );
        console.log(
          `Ответ на запрос Poincaré секций: ${response2.status}`
        );
        let trajectory = [];
        if (response2.ok) {
          let result2 = await response2.json();
          console.log("Полученные данные Poincaré секций:", result2);
          trajectory = result2 || [];
        } else {
          console.error("Failed to fetch Poincaré sections data");
        }

        // Второй запрос: Trajectories
        let response3 = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/trajectories/${result.id}`,
          {
            method: "GET",
          }
        );
        console.log(
          `Ответ на запрос траектории: ${response3.status}`
        );
        if (response3.ok) {
          let result3 = await response3.json(); // Исправлено на response3.json()
          console.log("Полученные данные траектории:", result3);
          let trajectory2 = result3 || [];
          if (trajectory2.length > 0) {
            const firstPoint = trajectory2[0];
            this.$emit("update-orbit-coordinates", {
              x: firstPoint.x,
              y: firstPoint.y,
              z: firstPoint.z,
              vx: firstPoint.vx,
              vy: firstPoint.vy,
              vz: firstPoint.vz,
            });
          } else {
            console.error("Trajectory data is empty.");
          }
        } else {
          console.error("Failed to fetch trajectory data");
        }

        this.orb4 = trajectory; // Можно решить, откуда брать данные для orb4
      } catch (error) {
        console.error(
          "NearestOrbitPoincare.vue: Ошибка при получении данных орбиты:",
          error
        );
        this.noOrbitInfoFound = true;
        this.resetData();
      } finally {
        this.localLoading = false; // Завершение локальной загрузки
        this.trajectoryLoading = false; // Завершение загрузки траектории
      }
    },
  },
};
</script>

