<!-- PlotlyGraph.vue -->
<template>
  <div ref="plot" class="plotly-graph"></div>
</template>

<script>
import Plotly from "plotly.js-dist-min";

export default {
  props: {
    dataPoints: {
      type: Array,
      required: true,
      default: () => [],
    },
    plane: {
      type: String,
      required: true,
      default: "x = 0",
    },
  },
  watch: {
    dataPoints: {
      handler(newVal) {
        this.$nextTick(() => {
          // Гарантируем, что DOM обновился
          if (newVal.length > 0) {
            this.renderPlot();
          } else {
            this.clearPlot();
          }
        });
      },
      deep: true,
      immediate: true,
    },
    plane() {
      this.$nextTick(() => {
        if (this.dataPoints.length > 0) {
          this.renderPlot();
        }
      });
    },
  },
  mounted() {
    this.$nextTick(() => {
      if (this.dataPoints.length > 0) {
        this.renderPlot();
      }
    });
  },
  methods: {
    /**
     * Форматирует число в строку без научной нотации.
     * @param {number} num - Число для форматирования.
     * @param {number} decimals - Количество знаков после запятой.
     * @returns {string} - Отформатированное число.
     */
    formatNumber(num, decimals = 4) {
      if (num === 0) return "0";
      // Используем фиксированное количество знаков после запятой
      return num
        .toLocaleString(undefined, {
          minimumFractionDigits: decimals,
          maximumFractionDigits: decimals,
        })
        .replace(/\.?0+$/, ""); // Удаление лишних нулей
    },

    /**
     * Метод для отрисовки графика.
     */
    renderPlot() {
      if (!this.$refs.plot) {
        console.error("Ref 'plot' is not available.");
        return;
      }

      // Определение осей на основе выбранной плоскости
      const planeMapping = {
        "x = 0": {
          x: "y",
          y: "z",
          titleX: "Y",
          titleY: "Z",
          unitX: "km",
          unitY: "km",
          scale: true, // Масштабирование
        },
        "y = 0": {
          x: "x",
          y: "z",
          titleX: "X",
          titleY: "Z",
          unitX: "km",
          unitY: "km",
          scale: true,
        },
        "z = 0": {
          x: "x",
          y: "y",
          titleX: "X",
          titleY: "Y",
          unitX: "km",
          unitY: "km",
          scale: true,
        },
        "vx = 0": {
          x: "vy",
          y: "vz",
          titleX: "Vy",
          titleY: "Vz",
          unitX: "km/s",
          unitY: "km/s",
          scale: false, // Не масштабируем
        },
        "vy = 0": {
          x: "vx",
          y: "vz",
          titleX: "Vx",
          titleY: "Vz",
          unitX: "km/s",
          unitY: "km/s",
          scale: false,
        },
        "vz = 0": {
          x: "vx",
          y: "vy",
          titleX: "Vx",
          titleY: "Vy",
          unitX: "km/s",
          unitY: "km/s",
          scale: false,
        },
      };

      const selectedPlane = this.plane;
      const axes = planeMapping[selectedPlane];

      if (!axes) {
        console.error(`Unknown plane: ${selectedPlane}`);
        return;
      }

      // Проверка, содержит ли хотя бы одна точка vx = 0, vy = 0 или vz = 0
      const hasZeroVelocity =
        selectedPlane === "vx = 0" ||
        selectedPlane === "vy = 0" ||
        selectedPlane === "vz = 0";

      // Определяем масштабирование и форматирование осей
      const scale = axes.scale && !hasZeroVelocity;
      const tickFormat = hasZeroVelocity ? ",.3f" : ",.0f";
      const scaledDataPoints = this.dataPoints.map((point) => ({
        x: scale ? point[axes.x] / 1000 : point[axes.x],
        y: scale ? point[axes.y] / 1000 : point[axes.y],
        actualX: point.x, // исходные значения для ховера
        actualY: point.y,
        actualZ: point.z,
        vx: point.vx,
        vy: point.vy,
        vz: point.vz,
        id: point.id, // Предполагается, что каждый объект имеет уникальный id
      }));

      const x = scaledDataPoints.map((p) => p.x);
      const y = scaledDataPoints.map((p) => p.y);
      const orbitIds = scaledDataPoints.map((p) => p.id);

      // Форматирование ховер-текста без научной нотации
      const hoverText = scaledDataPoints.map((p) => {
        return (
          `ID: ${p.id}<br>` +
          `X: ${this.formatNumber(p.actualX)} км<br>` +
          `Y: ${this.formatNumber(p.actualY)} км<br>` +
          `Z: ${this.formatNumber(p.actualZ)} км<br>` +
          `Vx: ${this.formatNumber(p.vx)} km/s<br>` +
          `Vy: ${this.formatNumber(p.vy)} km/s<br>` +
          `Vz: ${this.formatNumber(p.vz)} km/s`
        );
      });

      const data = [
        {
          x: x,
          y: y,
          mode: "markers",
          type: "scatter",
          marker: { size: 6, color: "blue" },
          text: hoverText,
          hoverinfo: "text",
          customdata: orbitIds, // Добавляем customdata для идентификации орбиты
        },
      ];

      const layout = {
        title: `Пуанкаре секции (${selectedPlane})`,
        xaxis: {
          title: `${axes.titleX} (${scale ? "10³ " : ""}${
            axes.unitX
          })`,
          tickformat: tickFormat,
          exponentformat: "none", // Отключение научной нотации
          zeroline: false,
          showgrid: true,
        },
        yaxis: {
          title: `${axes.titleY} (${scale ? "10³ " : ""}${
            axes.unitY
          })`,
          tickformat: tickFormat,
          exponentformat: "none", // Отключение научной нотации
          zeroline: false,
          showgrid: true,
        },
        showlegend: false,
        width: 700,
        height: 500,
        hovermode: "closest",
      };

      const config = {
        displayModeBar: false,
        responsive: true,
      };

      Plotly.react(this.$refs.plot, data, layout, config);
    },

    /**
     * Метод для очистки графика.
     */
    clearPlot() {
      if (this.$refs.plot) {
        Plotly.purge(this.$refs.plot);
      } else {
        console.warn("Plot reference is not available for clearing.");
      }
    },
  },
};
</script>

<style scoped>
.plotly-graph {
  width: 700px;
  height: 500px;
}
</style>
