<template>
  <div class="container">
    <!-- Индикатор загрузки -->
    <div v-if="combinedIsLoading" class="loading-message">
      <div class="spinner"></div>
      <p>Загрузка данных орбиты...</p>
    </div>

    <div v-else class="content">
      <div class="info">
        <h3>Информация об орбите</h3>
        <div class="min-container" v-if="!noOrbitInfoFound">
          <div
            class="point"
            v-for="(value, key) in orbitInfo"
            :key="key"
          >
            <p>{{ labels[key] }}</p>
            <p>{{ value }}</p>
          </div>
        </div>
        <div v-else class="no-orbits-message">
          <p>К сожалению, информация об орбите не найдена.</p>
        </div>
      </div>

      <div class="info-2">
        <h3>Визуализация орбиты</h3>
        <div>
          <div class="csv" v-if="!noOrbitInfoFound">
            <PlotlyCsv3d :csvData="orb4" class="csv-orb" />
          </div>
          <div v-else class="no-orbits-message">
            <p>К сожалению, данные графика орбиты не найдены.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PlotlyCsv3d from "@/components/mainPage/PlotlyCsv3d.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    PlotlyCsv3d,
  },
  props: {
    selectedOption: String,
    parentLoading: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    "update-orbit-coordinates",
    "update-family",
    "update-jacobi-constant",
  ],
  data() {
    return {
      orbitInfo: {
        period: null,
        amplitudeX: null,
        amplitudeY: null,
        amplitudeZ: null,
        jacobiConstant: null,
        stability: null,
        family: null,
      },
      orb4: [],
      noOrbitInfoFound: false,
      localLoading: false, // Локальное состояние загрузки
      labels: {
        period: "Период (дни)",
        amplitudeX: "Амплитуда по x (км)",
        amplitudeY: "Амплитуда по y (км)",
        amplitudeZ: "Амплитуда по z (км)",
        jacobiConstant: "Значение константы Якоби",
        stability: "Устойчивость",
        family: "Семейство",
      },
      familyOptions: [
        {
          label: "Горизонтальные орбиты Ляпунова, L1",
          value: "L1.L",
        },
        {
          label: "Горизонтальные орбиты Ляпунова, L2",
          value: "L2.L",
        },
        { label: "Вертикальные орбиты Ляпунова, L1", value: "L1.V" },
        { label: "Вертикальные орбиты Ляпунова, L2", value: "L2.V" },
        { label: "Аксиальные орбиты, L1", value: "L1.A" },
        { label: "Аксиальные орбиты, L2", value: "L2.A" },
        { label: "Гало орбиты, L1", value: "L1.H" },
        { label: "Гало орбиты, L2", value: "L2.H" },
        {
          label: "Двухпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.2P1",
        },
        {
          label: "Двухпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.2P1",
        },
        {
          label: "Трехпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.3P1",
        },
        {
          label: "Трехпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.3P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L1",
          value: "L1.L.4P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L2",
          value: "L2.L.4P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L1 (2P1.2P1)",
          value: "L1.L.2P1.2P1",
        },
        {
          label:
            "Четырехпериодические квазигоризонтальные орбиты, L2 (2P1.2P1)",
          value: "L2.L.2P1.2P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L1 (2P1.3P1)",
          value: "L1.L.2P1.3P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L1 (2P1.3P2)",
          value: "L1.L.2P1.3P2",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L2 (2P1.3P1)",
          value: "L2.L.2P1.3P1",
        },
        {
          label:
            "Шестипериодические квазигоризонтальные орбиты, L2 (2P1.3P2)",
          value: "L2.L.2P1.3P2",
        },
        {
          label:
            "Двухпериодические квазигоризонтальные орбиты, L1 (2P1.Tan)",
          value: "L1.L.2P1.Tan",
        },
        {
          label:
            "Двухпериодические квазигоризонтальные орбиты, L2 (2P1.Tan)",
          value: "L2.L.2P1.Tan",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P1)",
          value: "L1.H.2P1",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P2)",
          value: "L1.H.2P2",
        },
        {
          label: "Двухпериодические квазигало орбиты, L1 (2P3)",
          value: "L1.H.2P3",
        },
        {
          label: "Двухпериодические квазигало орбиты, L2 (2P1)",
          value: "L2.H.2P1",
        },
        {
          label: "Двухпериодические квазигало орбиты, L2 (2P2)",
          value: "L2.H.2P2",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P1)",
          value: "L1.H.3P1",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P2)",
          value: "L1.H.3P2",
        },
        {
          label: "Трехпериодические квазигало орбиты, L1 (3P3)",
          value: "L1.H.3P3",
        },
        {
          label: "Трехпериодические квазигало орбиты, L2 (3P1)",
          value: "L2.H.3P1",
        },
        { label: "Квазипериодические орбиты, L1", value: "L1.Q" },
      ],
    };
  },
  computed: {
    ...mapGetters(["orbitId", "getOrbitData"]),
    combinedIsLoading() {
      return this.parentLoading || this.localLoading;
    },
  },
  watch: {
    orbitId(newVal) {
      if (newVal) {
        this.loadOrbitData(newVal);
      } else {
        this.noOrbitInfoFound = true;
        this.resetData();
      }
    },
  },
  mounted() {
    if (this.orbitId) {
      this.loadOrbitData(this.orbitId);
    } else {
      this.noOrbitInfoFound = true;
    }
  },
  methods: {
    ...mapActions(["cacheOrbitData"]), // Добавляем cacheOrbitData

    resetData() {
      this.orbitInfo = {
        period: null,
        amplitudeX: null,
        amplitudeY: null,
        amplitudeZ: null,
        jacobiConstant: null,
        stability: null,
        family: null,
      };
      this.orb4 = [];
    },

    /**
     * Сопоставляет теги из API с метками из familyOptions.
     * @param {Array|string} tags - Теги, полученные от API.
     * @returns {string} - Читаемые метки, разделённые запятой.
     */
    mapFamilyTags(tags) {
      if (!tags) return "N/A";
      const tagsArray = Array.isArray(tags) ? tags : [tags];
      const tagToLabelMap = {};
      this.familyOptions.forEach((option) => {
        tagToLabelMap[option.value] = option.label;
      });
      const mappedLabels = tagsArray.map(
        (tag) => tagToLabelMap[tag] || tag
      );
      return mappedLabels.join(", ");
    },

    async submit() {
      this.isLoading = true; // Начало загрузки
      try {
        let response = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/orbits/${this.orbitId}`,
          {
            method: "GET",
          }
        );
        if (!response.ok) {
          this.noOrbitInfoFound = true;
          throw new Error("Failed to fetch orbit data");
        }
        let result = await response.json();

        if (!result || !result.t) {
          this.noOrbitInfoFound = true;
          return;
        }
        console.log(result.az);
        this.noOrbitInfoFound = false;

        // Присваивание с проверкой на null и undefined
        this.orbitInfo.period =
          result.t != null ? result.t.toFixed(2) : "N/A";
        this.orbitInfo.amplitudeX =
          result.ax != null ? result.ax.toFixed(2) : "N/A";
        this.orbitInfo.amplitudeY =
          result.ay != null ? result.ay.toFixed(2) : "N/A";
        this.orbitInfo.amplitudeZ =
          result.az !== undefined ? result.az.toFixed(2) : "N/A";
        this.orbitInfo.jacobiConstant =
          result.cj != null ? result.cj.toFixed(5) : "N/A";
        this.orbitInfo.stability = result.stable
          ? "Устойчива"
          : "Не устойчива";
        this.orbitInfo.family = this.mapFamilyTags(result.tags);

        // Получаем данные траектории из кеша Vuex или выполняем запрос
        let trajectory =
          this.getOrbitData(this.orbitId)?.trajectory || [];

        if (trajectory.length === 0) {
          // Если траектория не сохранена в кеше, выполняем запрос
          let response2 = await fetch(
            `https://orbital-catalog.auditory.ru/api/v1/trajectories/${result.id}`,
            {
              method: "GET",
            }
          );
          if (response2.ok) {
            let result2 = await response2.json();
            trajectory = result2 || [];
            if (trajectory.length > 0) {
              const firstPoint = trajectory[0];
              this.$emit("update-orbit-coordinates", {
                x: firstPoint.x,
                y: firstPoint.y,
                z: firstPoint.z,
                vx: firstPoint.vx,
                vy: firstPoint.vy,
                vz: firstPoint.vz,
              });
            } else {
              console.error("Trajectory data is empty.");
            }
          } else {
            console.error("Failed to fetch trajectory data");
          }
        } else {
          // Если траектория уже есть в кеше, используем ее
          if (trajectory.length > 0) {
            const firstPoint = trajectory[0];
            this.$emit("update-orbit-coordinates", {
              x: firstPoint.x,
              y: firstPoint.y,
              z: firstPoint.z,
              vx: firstPoint.vx,
              vy: firstPoint.vy,
              vz: firstPoint.vz,
            });
          }
        }

        this.orb4 = trajectory;

        // Эмитим остальные данные
        this.$emit("update-family", this.orbitInfo.family);
        this.$emit("update-jacobi-constant", result.cj || null);

        // Кешируем orbitInfo и trajectory
        this.cacheOrbitData({
          id: this.orbitId,
          orbitInfo: this.orbitInfo,
          trajectory: this.orb4,
        });
      } catch (error) {
        console.error("Error fetching orbit data:", error);
        this.noOrbitInfoFound = true;
        this.resetData();
      } finally {
        this.isLoading = false; // Завершение загрузки
      }
    },

    /**
     * Метод для загрузки данных орбиты.
     * @param {number|string} id - ID орбиты.
     */
    async loadOrbitData(id) {
      // Получаем данные орбиты из кеша Vuex
      const cachedData = this.getOrbitData(id);

      if (
        cachedData &&
        cachedData.orbitInfo &&
        cachedData.trajectory
      ) {
        console.log(
          `OrbitInformationBuild.vue: Используем кешированные данные для orbitId: ${id}`
        );
        this.setOrbitData(cachedData);
        return;
      }

      this.localLoading = true; // Начало локальной загрузки
      try {
        // Выполняем запрос к API для получения данных орбиты
        let response = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/orbits/${id}`,
          {
            method: "GET",
          }
        );
        if (!response.ok) {
          this.noOrbitInfoFound = true;
          throw new Error("Failed to fetch orbit data");
        }
        let result = await response.json();

        if (!result || !result.t) {
          this.noOrbitInfoFound = true;
          return;
        }

        this.noOrbitInfoFound = false;
        this.orbitInfo.period =
          result.t != null ? result.t.toFixed(2) : "N/A";
        this.orbitInfo.amplitudeX =
          result.ax != null ? result.ax.toFixed(2) : "N/A";
        this.orbitInfo.amplitudeY =
          result.ay != null ? result.ay.toFixed(2) : "N/A";
        this.orbitInfo.amplitudeZ =
          result.az !== null ? result.az.toFixed(2) : "N/A";
        this.orbitInfo.jacobiConstant =
          result.cj != null ? result.cj.toFixed(5) : "N/A";
        this.orbitInfo.stability = result.stable
          ? "Устойчива"
          : "Не устойчива";
        this.orbitInfo.family = this.mapFamilyTags(result.tags);

        // Получаем данные траектории из кеша Vuex или выполняем запрос
        let trajectory = cachedData ? cachedData.trajectory : [];

        if (trajectory.length === 0) {
          // Если траектория не сохранена в кеше, выполняем запрос
          let response2 = await fetch(
            `https://orbital-catalog.auditory.ru/api/v1/trajectories/${result.id}`,
            {
              method: "GET",
            }
          );
          if (response2.ok) {
            let result2 = await response2.json();
            trajectory = result2 || [];
            if (trajectory.length > 0) {
              const firstPoint = trajectory[0];
              this.$emit("update-orbit-coordinates", {
                x: firstPoint.x,
                y: firstPoint.y,
                z: firstPoint.z,
                vx: firstPoint.vx,
                vy: firstPoint.vy,
                vz: firstPoint.vz,
              });
            } else {
              console.error("Trajectory data is empty.");
            }
          } else {
            console.error("Failed to fetch trajectory data");
          }
        } else {
          // Если траектория уже есть в кеше, используем ее
          if (trajectory.length > 0) {
            const firstPoint = trajectory[0];
            this.$emit("update-orbit-coordinates", {
              x: firstPoint.x,
              y: firstPoint.y,
              z: firstPoint.z,
              vx: firstPoint.vx,
              vy: firstPoint.vy,
              vz: firstPoint.vz,
            });
          }
        }

        this.orb4 = trajectory;

        // Эмитим остальные данные
        this.$emit("update-family", this.orbitInfo.family);
        this.$emit("update-jacobi-constant", result.cj || null);

        // Кешируем orbitInfo и trajectory
        this.cacheOrbitData({
          id: id,
          orbitInfo: this.orbitInfo,
          trajectory: this.orb4,
        });
      } catch (error) {
        console.error(
          "OrbitInformationBuild.vue: Ошибка при получении данных орбиты:",
          error
        );
        this.noOrbitInfoFound = true;
        this.resetData();
      } finally {
        this.localLoading = false; // Завершение локальной загрузки
      }
    },

    /**
     * Устанавливает данные орбиты и кеширует их.
     * @param {Object} data - Данные орбиты.
     */
    setOrbitData(data) {
      if (data.orbitInfo && data.trajectory) {
        this.orbitInfo = { ...data.orbitInfo };
        this.orb4 = [...data.trajectory];
        this.noOrbitInfoFound = false;

        if (this.orb4.length > 0) {
          const firstPoint = this.orb4[0];
          this.$emit("update-orbit-coordinates", {
            x: firstPoint.x,
            y: firstPoint.y,
            z: firstPoint.z,
            vx: firstPoint.vx,
            vy: firstPoint.vy,
            vz: firstPoint.vz,
          });
        }

        // Кешируем orbitInfo и trajectory
        this.cacheOrbitData({
          id: this.orbitId,
          orbitInfo: this.orbitInfo,
          trajectory: this.orb4,
        });
      } else {
        this.noOrbitInfoFound = true;
        this.resetData();
      }
    },

    /**
     * Переходит на страницу построения орбиты.
     */
    goToBuildPage() {
      if (this.orbitId) {
        this.$router.push("/build");
      } else {
        console.error(
          "OrbitInformationBuild.vue: Orbit ID is not available."
        );
      }
    },
  },
};
</script>

