<!-- OrbitProjections.vue -->
<template>
  <div class="container">
    <h3>Проекции орбиты</h3>
    <!-- Индикатор загрузки -->
    <div v-if="combinedIsLoading" class="loading-message">
      <div class="spinner"></div>
      <p>Загрузка данных траектории...</p>
    </div>

    <div v-else-if="data && data.length" class="projections">
      <Plotly2DProjection :data="data" projection="xy" />
      <Plotly2DProjection :data="data" projection="xz" />
      <Plotly2DProjection :data="data" projection="yz" />
    </div>

    <div v-else class="no-data-message">
      <p>К сожалению, данные траектории не найдены.</p>
    </div>
  </div>
</template>

// OrbitProjections.vue
<script>
import Plotly2DProjection from "@/components/buildPage/Plotly2DProjection.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    Plotly2DProjection,
  },
  props: {
    orbitId: {
      type: [String, Number],
      required: true,
    },
  },
  emits: ["update-orbit-coordinates"],
  data() {
    return {
      data: [],
      isLoading: false, // Состояние загрузки
    };
  },
  computed: {
    ...mapGetters(["getTrajectory"]),
    combinedIsLoading() {
      return this.isLoading;
    },
  },
  watch: {
    orbitId(newVal) {
      console.log(
        `OrbitProjections.vue: orbitId изменился на ${newVal}`
      );
      if (newVal) {
        this.fetchTrajectoryData(newVal);
      } else {
        this.data = [];
      }
    },
  },
  created() {
    if (this.orbitId) {
      console.log(
        `OrbitProjections.vue: Создаёмся с orbitId: ${this.orbitId}`
      );
      this.fetchTrajectoryData(this.orbitId);
    } else {
      console.error("OrbitProjections.vue: orbitId не найден в URL.");
    }
  },
  methods: {
    ...mapActions(["cacheTrajectoryData"]),
    async fetchTrajectoryData(id) {
      console.log(
        `OrbitProjections.vue: Начинаем загрузку траектории для orbitId: ${id}`
      );
      this.isLoading = true; // Начало загрузки
      try {
        // Проверяем, есть ли данные в кеше Vuex
        const cachedTrajectory = this.getTrajectory(id);
        if (cachedTrajectory && cachedTrajectory.length > 0) {
          console.log(
            `OrbitProjections.vue: Используем кешированные данные траектории для orbitId: ${id}`
          );
          this.data = cachedTrajectory;
          this.emitFirstPoint(cachedTrajectory);
          return;
        }

        // Если данных нет в кеше, выполняем запрос к API
        const trajectoryResponse = await fetch(
          `https://orbital-catalog.auditory.ru/api/v1/trajectories/${id}`,
          {
            method: "GET",
          }
        );
        if (!trajectoryResponse.ok) {
          throw new Error("Не удалось получить данные траектории.");
        }
        const trajectoryData = await trajectoryResponse.json();
        console.log(
          "OrbitProjections.vue: Полученные данные траектории:",
          trajectoryData
        );

        this.data = trajectoryData || [];

        if (this.data.length > 0) {
          this.emitFirstPoint(this.data);
          // Кешируем данные траектории в Vuex
          this.cacheTrajectoryData({
            id: id,
            trajectory: this.data,
          });
          console.log(
            `OrbitProjections.vue: Кешируем траекторию для orbitId: ${id}`
          );
        } else {
          console.warn(
            "OrbitProjections.vue: Данные траектории пусты."
          );
        }
      } catch (error) {
        console.error(
          "OrbitProjections.vue: Ошибка при получении данных траектории:",
          error
        );
        this.data = [];
      } finally {
        this.isLoading = false; // Завершение загрузки
        console.log(
          `OrbitProjections.vue: Завершили загрузку траектории для orbitId: ${id}`
        );
      }
    },
    emitFirstPoint(trajectory) {
      const firstPoint = trajectory[0];
      if (firstPoint && firstPoint.x !== undefined) {
        console.log(
          "OrbitProjections.vue: Эмитируем координаты первой точки траектории:",
          firstPoint
        );
        this.$emit("update-orbit-coordinates", {
          x: firstPoint.x,
          y: firstPoint.y,
          z: firstPoint.z,
          vx: firstPoint.vx,
          vy: firstPoint.vy,
          vz: firstPoint.vz,
        });
      } else {
        console.warn(
          "OrbitProjections.vue: Первый пункт траектории не содержит необходимых данных."
        );
      }
    },
  },
};
</script>

