<template>
  <div ref="plot" class="plotly-graph"></div>
</template>

<script>
import Plotly from "plotly.js-dist-min";

export default {
  props: {
    dataPoints: {
      type: Array,
      required: true,
    },
    paramX: {
      type: String,
      required: true,
    },
    paramY: {
      type: String,
      required: true,
    },
  },
  watch: {
    dataPoints: {
      handler() {
        this.renderPlot();
      },
      deep: true,
    },
    paramX: "renderPlot",
    paramY: "renderPlot",
  },
  mounted() {
    this.renderPlot();
  },
  methods: {
    renderPlot() {
      if (!this.$refs.plot) {
        console.error("Ref 'plot' is not available.");
        return;
      }

      const paramFieldMapping = {
        Период: "t",
        "Амплитуда по X": "ax",
        "Амплитуда по Y": "ay",
        "Амплитуда по Z": "az",
        "Расстояние до Земли": "dist_primary",
        "Расстояние до Луны": "dist_secondary",
        "Константа Якоби": "cj",
        "Максимальный множитель Флоке": "max_floke",
      };

      const fieldX = paramFieldMapping[this.paramX];
      const fieldY = paramFieldMapping[this.paramY];

      if (!fieldX || !fieldY) {
        console.error("Invalid parameter selected");
        return;
      }

      const x = this.dataPoints.map((p) => p[fieldX]);
      const y = this.dataPoints.map((p) => p[fieldY]);

      // Создаем ховер-текст
      const hoverText = this.dataPoints.map((p) => {
        let text = "";
        for (const [key, value] of Object.entries(p)) {
          text += `${key}: ${value}<br>`;
        }
        return text;
      });

      const data = [
        {
          x: x,
          y: y,
          mode: "markers",
          type: "scatter",
          marker: { size: 6 },
          text: hoverText,
          hoverinfo: "text",
        },
      ];

      const layout = {
        xaxis: {
          title: this.paramX,
        },
        yaxis: {
          title: this.paramY,
        },
        showlegend: false,
        width: 700,
        height: 500,
        hovermode: "closest",
      };

      const config = {
        displayModeBar: false,
        responsive: true,
      };

      Plotly.react(this.$refs.plot, data, layout, config);
    },
  },
};
</script>

<style scoped>
.plotly-graph {
  width: 700px;
  height: 500px;
}
</style>
